<!--  -->
<template>
  <swiper
    :options="swiperOptions"
    :breakpoints="breakpoints"
    navigation
    :pagination="{ clickable: true }"
  >
    <swiper-slide v-for="product in filterProducts" :key="product.id">
      <ProductCard :product="product"> </ProductCard>
    </swiper-slide>
    <div class="swiper-pagination"></div>
  </swiper>
</template>

<script>
// Import Swiper Vue.js components
// import Swiper core and required modules
import SwiperCore, {
  Navigation, Pagination, Scrollbar, A11y,
} from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';

// '' 
import ProductCard from './ProductCard.vue';

// install Swiper modules
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

export default {
  components: {
    Swiper,
    SwiperSlide,
    ProductCard,
  },
  props: {
    // 
    limitCount: {
      type: Number,
      default() {
        return 4;
      },
    },
    // 
    filterProduct: {
      type: Object,
      default() {
        return {
          name: '',
          category: '',
        };
      },
    },
  },
  watch: {
    // 
    filterProduct() {
      this.getProducts();
    },
  },
  data() {
    return {
      // API
      baseAPI: `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}`,
      // 
      products: [],
      filterProducts: [],
      recStr: '',
      productCount: 1,
      // Swiper
      swiperOptions: {
        pagination: '.swiper-pagination',
      },
      breakpoints: {
        1200: {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        768: {
          slidesPerView: 3,
          spaceBetween: 20,
        },
        480: {
          slidesPerView: 2,
          spaceBetween: 10,
        },
        320: {
          slidesPerView: 1,
          spaceBetween: 10,
        },
      },
      tips: {
        data: {
          success: false,
          message: '!',
        },
      },
    };
  },
  inject: ['$httpMessageState'],
  methods: {
    //  ()
    getProducts() {
      const api = `${this.baseAPI}/products/all`;
      this.$http
        .get(api)
        .then((response) => {
          if (response.data.success) {
            this.products = response.data.products;
            // 
            const recStrCategory = this.filterProduct.category !== '' ? this.filterProduct.category : '';
            // 
            this.productCount = 0;
            this.filterProducts = [];

            this.products.forEach((item) => {
              if (recStrCategory !== '') {
                //  ()
                if (item.id !== this.filterProduct.id && item.category === recStrCategory) {
                  if (this.productCount < this.limitCount) {
                    const tempItem = { ...item };
                    this.filterProducts.push(tempItem);
                    this.productCount += 1;
                  }
                }
              } else if (item.title.includes(this.recStr)) {
                // 
                if (this.productCount < this.limitCount) {
                  const tempItem = { ...item };
                  this.filterProducts.push(tempItem);
                  this.productCount += 1;
                }
              }
            });
          } else {
            this.$httpMessageState(response, '');
          }
        })
        .catch(() => {
          this.$httpMessageState(this.tips, '');
        });
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>
